
"use server";

import { generateCoachingRationale } from "@/ai/flows/generate-coaching-rationale";
import { coachingProfiles, personalityTypes } from "./data";
import type { GenerateCoachingRationaleInput } from "@/ai/flows/generate-coaching-rationale";
import type { PersonalityType, Recommendation, Trait, TraitScore } from "./types";
import { ai } from "@/ai/genkit";

export async function getCoachingRecommendations(
  answers: Record<number, Trait>
): Promise<{
  personalityType: PersonalityType;
  traitScores: TraitScore[];
  recommendations: Recommendation[];
}> {
  // 1. Calculate trait scores
  const traitScoresMap = new Map<Trait, number>();
  for (const trait of Object.values(answers)) {
    traitScoresMap.set(trait, (traitScoresMap.get(trait) || 0) + 1);
  }

  const traitScores: TraitScore[] = Array.from(traitScoresMap, ([trait, score]) => ({ trait, score }));
  
  const sortedTraits = traitScores.sort((a, b) => b.score - a.score);
  
  // 2. Determine dominant trait and personality type
  const dominantTrait = sortedTraits.length > 0 ? sortedTraits[0].trait : 'balanced-pace';
  
  const personalityType =
    personalityTypes.find((p) => p.trait === dominantTrait) ||
    personalityTypes.find(p => p.trait === 'balanced-pace')!;

  const studentTraits = sortedTraits.slice(0, 3).map(t => t.trait);

  // 3. Match with coaching profiles
  const scoredCoachings = coachingProfiles.map((coaching) => {
    const score = coaching.tags.reduce((acc, tag) => {
      if (studentTraits.includes(tag)) {
        return acc + 1;
      }
      return acc;
    }, 0);
    return { ...coaching, score };
  });

  const top3Coaching = scoredCoachings.sort((a, b) => b.score - a.score).slice(0, 3);

  // 4. Return recommendations without AI content, which will be fetched on the client
  const recommendations: Recommendation[] = top3Coaching.map((coaching) => {
    return {
      ...coaching,
      rationale: {
        goodFitRationale: '',
        badFitRationale: '',
      },
    };
  });


  return { personalityType, traitScores, recommendations };
}

export async function streamCoachingRationale(input: GenerateCoachingRationaleInput) {
    const {stream} = ai.runFlow(generateCoachingRationale, input);
    return stream;
}
