
"use client";

import Image from "next/image";
import type { PersonalityType, Recommendation, TraitScore } from "@/lib/types";
import { Button } from "./ui/button";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "./ui/card";
import { ArrowRight, CheckCircle2, RefreshCw, ShieldCheck, XCircle } from "lucide-react";
import Link from "next/link";
import { Badge } from "./ui/badge";
import { PolarAngleAxis, PolarGrid, Radar, RadarChart } from "recharts";
import { ChartConfig, ChartContainer, ChartTooltip, ChartTooltipContent } from "./ui/chart";
import { traits } from "@/lib/data";
import { motion } from "framer-motion";
import { Skeleton } from "./ui/skeleton";


interface QuizResultsProps {
  personalityType: PersonalityType;
  traitScores: TraitScore[];
  recommendations: Recommendation[];
  onRestart: () => void;
  onRetry: (recommendation: Recommendation) => void;
}

const chartConfig = {
  score: {
    label: "Score",
    color: "hsl(var(--primary))",
  },
} satisfies ChartConfig;

const RationaleDisplay = ({ rationale, onRetry }: { rationale: Recommendation['rationale'], onRetry: () => void }) => {
    if (rationale.error) {
        return (
            <div className="space-y-3 text-sm">
                <div className="flex items-start gap-2 text-destructive">
                    <XCircle className="w-5 h-5 mt-0.5 shrink-0" />
                    <p>There was an issue generating the rationale for this recommendation.</p>
                </div>
                <Button variant="link" size="sm" onClick={onRetry} className="p-0 h-auto">
                    <RefreshCw className="mr-2 h-4 w-4" />
                    Click to try again
                </Button>
            </div>
        );
    }
    
    if (!rationale.goodFitRationale && !rationale.badFitRationale) {
      return (
        <div className="space-y-3 text-sm mt-4">
            <div className="flex items-start gap-2">
                <Skeleton className="w-5 h-5 rounded-full mt-0.5 shrink-0" />
                <div className="space-y-2 flex-1">
                    <Skeleton className="h-4 w-full" />
                    <Skeleton className="h-4 w-4/5" />
                </div>
            </div>
            <div className="flex items-start gap-2">
                <Skeleton className="w-5 h-5 rounded-full mt-0.5 shrink-0" />
                <div className="space-y-2 flex-1">
                    <Skeleton className="h-4 w-full" />
                    <Skeleton className="h-3/5" />
                </div>
            </div>
        </div>
      );
    }

    return (
        <div className="space-y-3 text-sm">
            <div className="flex items-start gap-2">
                <CheckCircle2 className="w-5 h-5 text-green-500 mt-0.5 shrink-0" />
                <p className="text-muted-foreground">{rationale.goodFitRationale}</p>
            </div>
            <div className="flex items-start gap-2">
                <XCircle className="w-5 h-5 text-destructive mt-0.5 shrink-0" />
                <p className="text-muted-foreground">{rationale.badFitRationale}</p>
            </div>
        </div>
    );
};

const containerVariants = {
    hidden: { opacity: 1 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.3,
      },
    },
};
  
const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        type: "spring",
        stiffness: 260,
        damping: 20,
      },
    },
};

export default function QuizResults({ personalityType, traitScores, recommendations, onRestart, onRetry }: QuizResultsProps) {
  const chartData = traits
    .map(trait => {
        const score = traitScores.find(s => s.trait === trait.id);
        return {
            trait: trait.name,
            score: score ? score.score : 0,
            fill: "hsl(var(--primary))",
        };
    });
  
  return (
    <motion.div
      variants={containerVariants}
      initial="hidden"
      animate="visible"
    >
      <motion.div variants={itemVariants} className="text-center mb-8">
        <h2 className="text-base font-semibold text-primary tracking-wide uppercase">Your Personality Type</h2>
        <p className="mt-2 text-4xl font-extrabold tracking-tight sm:text-5xl text-gradient">
          {personalityType.name}
        </p>
        <p className="mt-4 max-w-xl mx-auto text-lg text-muted-foreground">
          {personalityType.description}
        </p>
      </motion.div>

      <motion.div variants={itemVariants}>
        <Card className="glowing-card mb-8">
          <CardHeader>
            <CardTitle>Your Trait Analysis</CardTitle>
            <CardDescription>This chart shows how your answers align with different learning traits.</CardDescription>
          </CardHeader>
          <CardContent className="pb-0">
            <ChartContainer config={chartConfig} className="mx-auto aspect-square max-h-[250px]">
              <RadarChart data={chartData}>
                <ChartTooltip
                  cursor={false}
                  content={<ChartTooltipContent indicator="line" />}
                />
                <PolarGrid />
                <PolarAngleAxis dataKey="trait" />
                <Radar
                  dataKey="score"
                  fill="var(--color-score)"
                  fillOpacity={0.6}
                  dot={{
                    r: 4,
                    fillOpacity: 1,
                  }}
                />
              </RadarChart>
            </ChartContainer>
          </CardContent>
        </Card>
      </motion.div>


      <motion.div 
        className="space-y-6"
        variants={containerVariants}
      >
        {recommendations.map((rec, index) => (
          <motion.div key={rec.id} variants={itemVariants}>
            <Card className="glowing-card overflow-hidden">
              <div className="md:flex">
                <div className="md:w-1/3 md:shrink-0 relative">
                  <Image
                    src={rec.image.imageUrl}
                    alt={rec.name}
                    width={600}
                    height={400}
                    data-ai-hint={rec.image.imageHint}
                    className={`h-48 w-full object-cover md:h-full`}
                    priority={index === 0}
                  />
                </div>
                <div className="p-6 flex flex-col flex-1">
                  <CardHeader className="p-0 mb-4">
                    <div className="flex justify-between items-start">
                      <CardTitle className="text-2xl font-bold">{rec.name}</CardTitle>
                      {index === 0 && (
                        <Badge variant="default" className="flex items-center gap-1">
                          <ShieldCheck className="w-4 h-4" />
                          Top Match
                        </Badge>
                      )}
                    </div>
                    <CardDescription>{rec.description}</CardDescription>
                  </CardHeader>
                  <CardContent className="p-0 flex-1">
                    <h4 className="font-semibold mb-2">AI-Powered Rationale:</h4>
                    <RationaleDisplay rationale={rec.rationale} onRetry={() => onRetry(rec)} />
                  </CardContent>
                  <CardFooter className="p-0 mt-6">
                    <Button asChild>
                      <Link href={rec.reviewUrl} target="_blank" rel="noopener noreferrer">
                        Explore Reviews <ArrowRight className="ml-2 h-4 w-4" />
                      </Link>
                    </Button>
                  </CardFooter>
                </div>
              </div>
            </Card>
          </motion.div>
        ))}
      </motion.div>

      <motion.div variants={itemVariants} className="mt-8 text-center">
        <Button variant="outline" onClick={onRestart}>
          <RefreshCw className="mr-2 h-4 w-4" />
          Take the Quiz Again
        </Button>
      </motion.div>
    </motion.div>
  );
}
